import React from 'react';
import axios from 'axios';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import AccountCircle from '@material-ui/icons/AccountCircle';
import { Redirect } from 'react-router-dom'

class StudentListItem extends React.Component {
  state = {
    redirect: false
  }
  constructor(props) {
    super(props);
    this.fullName = `${this.props.lastName}, ${this.props.firstName}`
    this.average = `Average: ${this.props.average}`
    this.id = this.props.id
  }

  render() {
    let redirectElement = <Redirect to={`students/?id=${this.id}`} />
    
    return (
        <ListItem
          className="studentListItem"
          button
          onClick={event => this.redirect(event)}
        >
        {this.state.redirect ? redirectElement : null}
          <ListItemIcon>
            <AccountCircle />
          </ListItemIcon>
          <ListItemText primary={this.fullName} secondary={this.average} />
            <ListItemSecondaryAction>
              <IconButton onClick={ () => this.deleteStudent(this.props.id)} aria-label="Delete">
                <DeleteIcon />
              </IconButton>
            </ListItemSecondaryAction>
        </ListItem>
      
    );
  }

  deleteStudent(id) {
    
    axios.delete('/api/students/'+id+'/', {
      'student': id
    })
    .then(response => {
      this.props.updateParent()
    })
    .catch(error => {
      console.log(error.response)
    })
  }

  redirect() {
    console.log('redirecting...');
    this.setState({redirect: true});
  }

}

export default StudentListItem;
