import React from 'react';
import axios from 'axios';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add'
import { withStyles } from '@material-ui/core/styles';

const styles = theme => ({
    container: {
        marginTop: '15px',
        marginLeft: 'auto',
        marginRight: 'auto',
        width: 'fit-content'
    },
    formControl: {
        marginRight:  '10px'
    }
  });

  class ExamForm extends React.Component {
    constructor(props) {
      super(props)
      this.state = {
        examName: ''
      }
    }
  
    addExam() {
        axios.post('/api/exams/', {
          'name': this.state.examName,
        })
        .then(response => {
          this.props.updateParent();
          this.setState({examName: ''});
          this.props.changeExamFocus(response.data.id);
        })
        .catch(error => {
          console.log(error.response)
        })
      }

    handleExamNameChange(event) {
        this.setState({ examName: event.target.value });
    };
  
    render() {
      const { classes } = this.props;
  
      return (
        <div className={classes.container}>
          <FormControl className={classes.formControl}>
             <InputLabel htmlFor="examname-input">New Exam</InputLabel>
             <Input id="examname-input" value={this.state.examName} onChange={(e) => this.handleExamNameChange(e)} />
          </FormControl>
          <Fab onClick={ () => this.addExam(this.props.id)} color="primary" aria-label="Add">
            <AddIcon />
          </Fab>
          
        </div>
      );
    }
  }
  
  export default withStyles(styles)(ExamForm);
  