import React from 'react';
import { Element, withFauxDOM } from 'react-faux-dom';
import * as d3 from 'd3';
import './chart.css';

class Barchart extends React.Component {

    drawChart() {
        const margin = 60;
        const width = 1000 - 2 * margin;
        const height = 600 - 2 * margin;

        const el = new Element('div');
        el.setAttribute('id', 'container');
        const fauxSvg = new Element('svg');
        el.appendChild(fauxSvg)

        const svg = d3.select(fauxSvg);
        const chart = svg.append('g')
            .attr('transform', `translate(${margin}, ${margin})`);


        const yScale = d3.scaleLinear()
            .range([height, 0])
            .domain([0, 100]);

        chart.append('g')
            .call(d3.axisLeft(yScale));

        const xScale = d3.scaleBand()
            .range([0, width])
            .domain(this.props.data.map((exam) => exam.name))
            .padding(0.2)

        chart.append('g')
            .attr('transform', `translate(0, ${height})`)
            .call(d3.axisBottom(xScale));

        const barGroups = chart.selectAll()
            .data(this.props.data)
            .enter()
            .append('rect')
            .attr('class', 'bar')
            .attr('x', (exam) => xScale(exam.name))
            .attr('y', (exam) => yScale(exam.average))
            .attr('height', (exam) => height - yScale(exam.average))
            .attr('width', xScale.bandwidth())
            
        chart.append('g')
            .attr('class', 'grid')
            .attr('transform', `translate(0, ${height})`)
            .call(d3.axisBottom()
                .scale(xScale)
                .tickSize(-height, 0, 0)
                .tickFormat(''))
        
        chart.append('g')
            .attr('class', 'grid')
            .call(d3.axisLeft()
                .scale(yScale)
                .tickSize(-width, 0, 0)
                .tickFormat(''))

        svg.append('text')
            .attr('x', -(height / 2) - margin)
            .attr('y', margin / 2.4)
            .attr('transform', 'rotate(-90)')
            .attr('text-anchor', 'middle')
            .text('Score (%)')

        svg.append('text')
            .attr('x', width / 2 + margin)
            .attr('y', 40)
            .attr('text-anchor', 'middle')
            .text('Average Exam Scores')

        return el.toReact();
    }
    
    render() {
        return this.drawChart();
    }
}

export default withFauxDOM(Barchart);