import React from 'react'
import axios from 'axios';
import StudentInfoListItem from './StudentInfoListItem'
import { withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';

const styles = theme => ({
  container: {
      marginTop: '15px',
      marginLeft: 'auto',
      marginRight: 'auto',
      width: 'fit-content'
  },
  formControl: {
      marginRight:  '10px'
  }
});

class StudentInfo extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            gradesList: [],
            loading: true
        }
        this.getData = this.getData.bind(this)
      }

    componentWillMount () {
        this.getData();
    }

    createList = () => {
        let listItems = []
        if (!this.state.loading) {
            this.state.gradesList.map((exam) => {
                listItems.push(
                <StudentInfoListItem
                key = {exam.id}
                id = {exam.id}
                examName = {exam.exam}
                score={exam.score}
                updateParent = {this.getData}
                />
                )
                return listItems
            })
            return listItems
        }
    }  
    
    getData() {
    axios.get('/api/students/' + this.props.id + '/')
    .then(response => {
        this.setState({   
            studentInfo: response.data,
            gradesList: response.data.grades,
            loading: false
        });
    })
    }

    render() {
        const { classes } = this.props;

        return (
        <div className={classes.container}>
            <List className={classes.container}>
                {this.createList()}
            </List>
        </div>
        );
    }
}

export default withStyles(styles)(StudentInfo);


