import React from 'react'
import ExamForm from './ExamForm';
import ExamInput from './ExamInput';
import axios from 'axios';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import ExamList from './ExamList';


const styles = theme => ({
  root: {
    flexGrow: 1,
    margin: 10,
  },
  examList: {
    maxHeight: 50,
    overflow: 'scroll',
    backgroundColor: '#000'
  },
  paper: {
    padding: theme.spacing.unit * 2,
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
});

class Exams extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      loading: true
    }
    this.getData = this.getData.bind(this)
    this.changeExamFocus = this.changeExamFocus.bind(this)
  }

  componentDidMount() {
    this.getData();
  }

  getData() {
    axios.get('/api/exams/')
    .then(response => {
      this.setState({   
          examList: response.data,
          selectedExam: this.getSelectedExam(response),
          loading: false
      });
    })
  }

  getSelectedExam(response) {
    if (response) {
      if (this.state.selectedExam == null) {
        if (response.data[0]) {
          return response.data[0].id
        }
      }
    }
  }

  changeExamFocus(newId) {
    this.setState({selectedExam: newId});
  }

  render() {
    const { classes } = this.props;
    return (
      <div className={classes.root}>
        <Grid container spacing={24}>
          <Grid item md={4} sm={12} xs={12}>
            <Paper className={classes.paper}>
              { this.state.loading ? null : 
                <ExamForm updateParent = {this.getData} changeExamFocus = {this.changeExamFocus} />
              }
              { this.state.loading ? null : 
                <ExamList className={classes.examList} updateParent = {this.getData} changeExamFocus = {this.changeExamFocus} data={this.state.examList}/>
              }
            </Paper>
          </Grid>
          <Grid item md={8} sm={12} xs={12}>
            <Paper className={classes.paper}>
              { this.state.loading ? null : 
                <ExamInput id = {this.state.selectedExam} updateParent = {this.getData} />
              }
              
            </Paper>
          </Grid>
        </Grid>
      </div>
      
    );
  }
}

export default withStyles(styles)(Exams);


