import React from 'react'
import axios from 'axios';
import StudentGradeListItem from './StudentGradeListItem';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import SaveIcon from '@material-ui/icons/Save';

const styles = theme => ({
    button: {
        marginTop: '15px',
    }
});

class ExamInput extends React.Component {
    
    constructor(props) {
        super(props)
        this.updateScore = this.updateScore.bind(this)
        this.saveBatch = this.saveBatch.bind(this)
        this.updatedScores = {};
      }

    componentDidMount() {
        this.getData(this.props.id);
    }
    componentWillReceiveProps(newProps) {
        if (newProps.id !== this.props.id){
          this.getData(newProps.id);
        }
      }
    componentDidUpdate() {
        this.updatedScores = {};
    }

    getData(id) {
        axios.get('api/exams/' + id + '/')
        .then(response => {
            this.setState({   
                examName: response.data.name,
                gradesList: response.data.grades,
                average: response.data.average,
                loading: false
            });
        })
        .catch((err) => {
            console.log(err);
        })
    }

    updateScore(id, score) {
        this.updatedScores[id] = score
    }

    saveBatch() {
        let url = '/api/grade-batch'
        axios.post(url, {
            'updatedScores': this.updatedScores
        })
        .then(response => {
            this.getData(this.props.id);
        })
        .catch(error => {
          console.log(error.response)
        })
      }

    createList = () => {
        let listItems = []
        if (!this.state.loading) {
            this.state.gradesList.map((grade, idx) => {
                listItems.push(
                <StudentGradeListItem
                idx = {idx}
                key = {grade.id}
                id = {grade.id}
                studentName = {grade.studentName}
                score={grade.score}
                updateParent = {this.getData}
                updateScore = {this.updateScore}
                />
                )
                return listItems
            })
            return listItems
        }
    }  

    render() {
        const { classes } = this.props;
        return (
            <div>
                {this.state && this.state.examName &&
                    <h1>{this.state.examName}</h1>
                }
                {this.state && this.state.examName &&
                    <h3>Average score: {this.state.average}</h3>
                }
                {this.state && this.state.examName &&
                    this.createList()
                }
                <Button onClick={this.saveBatch} className={classes.button} variant="contained" size="small">
                    <SaveIcon />
                    Save
                </Button>
            </div>
            
        )
    }

}

export default withStyles(styles)(ExamInput);