import React from 'react';
import axios from 'axios';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add'
import { withStyles } from '@material-ui/core/styles';

const styles = theme => ({
    container: {
        marginTop: '15px',
        marginLeft: 'auto',
        marginRight: 'auto',
        width: 'fit-content'
    },
    formControl: {
        marginRight:  '10px'
    }
  });

  class StudentForm extends React.Component {
    state = {
      firstName: '',
      lastName: '',
    };
  
    addStudent() {
        axios.post('/api/students/', {
          'firstName': this.state.firstName,
          'lastName': this.state.lastName
        })
        .then(response => {
          this.props.updateParent()
        })
        .catch(error => {
          console.log(error.response)
        })
      }

    handleFirstNameChange(event) {
        this.setState({ firstName: event.target.value });
    };
    handleLastNameChange(event) {
        this.setState({ lastName: event.target.value });
    };
  
    render() {
      const { classes } = this.props;
  
      return (
        <div className={classes.container}>
          <FormControl className={classes.formControl}>
            <InputLabel htmlFor="firstname-input">First Name</InputLabel>
            <Input id="firstname-input" value={this.state.firstName} onChange={(e) => this.handleFirstNameChange(e)} />
          </FormControl>
          <FormControl className={classes.formControl}>
            <InputLabel htmlFor="lastname-input">Last Name</InputLabel>
            <Input id="lastname-input" value={this.state.lastName} onChange={(e) => this.handleLastNameChange(e)} />
          </FormControl>
          <Fab onClick={ () => this.addStudent(this.props.id)} color="primary" aria-label="Add">
            <AddIcon />
          </Fab>
          
        </div>
      );
    }
  }
  
  export default withStyles(styles)(StudentForm);
  