import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';

const styles = theme => ({
    button: {
        marginTop: '15px',
    }
  });


class StudentGradeListItem extends React.Component {
    state = {
        score: this.props.score
      };

  render() {
    const { classes } = this.props;

    return (
        <div className={classes.container}>
            <FormControl className={classes.formControl}>
                {this.props.idx === 0 ? <InputLabel htmlFor="student-name">Student Name</InputLabel> : null}
                <Input disabled id="student-name" value={this.props.studentName} />
            </FormControl>
            <FormControl className={classes.formControl}>
            {this.props.idx === 0 ? <InputLabel htmlFor="score">Score</InputLabel> : null}
                <Input id="score" value={this.state.score} onChange={(e) => this.handleScoreChange(e)} />
            </FormControl>
        </div>
    );
  }

  handleScoreChange(event) {
    this.setState({ score: event.target.value });
    this.props.updateScore(this.props.id, event.target.value)
  }


  

}

export default withStyles(styles)(StudentGradeListItem);
