import React from 'react';
import axios from 'axios';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import ListAltIcon from '@material-ui/icons/ListAlt';

class ExamListItem extends React.Component {
  state = {
    redirect: false
  }
  constructor(props) {
    super(props);
    this.average = `Average: ${this.props.average}`
    this.id = this.props.id
  }

  render() {
    
    return (
        <ListItem
          button
          onClick= {() => this.props.changeExamFocus(this.props.id)}
        >
          <ListItemIcon>
            <ListAltIcon />
          </ListItemIcon>
          <ListItemText primary={this.props.examName} secondary={this.average} />
            <ListItemSecondaryAction>
              <IconButton onClick={ () => this.deleteExam(this.props.id)} aria-label="Delete">
                <DeleteIcon />
              </IconButton>
            </ListItemSecondaryAction>
        </ListItem>
      
    );
  }
  changeExamFocus(id) {
      this.props.changeExamFocus(id);
  }

  deleteExam(id) {
    
    axios.delete('/api/exams/'+id+'/', {
      'id': id
    })
    .then(response => {
      this.props.updateParent()
    })
    .catch(error => {
      console.log(error.response)
    })
  }

}

export default ExamListItem;
