import React from 'react';
import Edit from '@material-ui/icons/Edit';
import Save from '@material-ui/icons/Save';
import Cancel from '@material-ui/icons/Cancel';
import IconButton from '@material-ui/core/IconButton';
import { withStyles } from '@material-ui/core/styles';
import axios from 'axios';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';

const styles = theme => ({
    container: {
        marginTop: '15px',
        marginLeft: 'auto',
        marginRight: 'auto',
        width: 'fit-content'
    },
    formControl: {
        marginRight:  '10px'
    }
  });


class StudentInfoListItem extends React.Component {
    state = {
        isBeingEdited: false,
        score: this.props.score
      };

  render() {
    const { classes } = this.props;
    let uneditableScoreInput = 
        <FormControl className={classes.formControl}>
            <InputLabel htmlFor="score">Score</InputLabel>
            <Input disabled id="score" value={this.props.score} />
        </FormControl>

    let editableScoreInput = 
        <FormControl className={classes.formControl}>
            <InputLabel htmlFor="score">Score</InputLabel>
            <Input id="score" value={this.state.score} onChange={(e) => this.handleScoreChange(e)} />
        </FormControl>

    let editButton = 
        <FormControl className={classes.formControl}>
            <IconButton onClick={ () => this.editItem(this.props.id)} aria-label="Edit">
                <Edit />
            </IconButton>
          </FormControl>

    let cancelButton = 
        <FormControl className={classes.formControl}>
            <IconButton onClick={ () => this.editItem(this.props.id)} aria-label="Cancel">
                <Cancel />
            </IconButton>
          </FormControl>

    let saveButton = 
        <FormControl className={classes.formControl}>
            <IconButton onClick={ () => this.saveItem(this.props.id)} aria-label="Save">
                <Save />
            </IconButton>
          </FormControl>
    
    
    return (
        <div className={classes.container}>
          <FormControl className={classes.formControl}>
            <InputLabel htmlFor="exam-name">Exam Name</InputLabel>
            <Input disabled id="exam-name" value={this.props.examName} />
          </FormControl>

        {this.state.isBeingEdited ? editableScoreInput : uneditableScoreInput }
          
        {this.state.isBeingEdited ? saveButton : editButton }
        {this.state.isBeingEdited ? cancelButton : null }

        </div>
    );
  }

  handleScoreChange(event) {
    this.setState({ score: event.target.value });
  }

  editItem(id) {
    this.setState(prevState => ({
        isBeingEdited: !prevState.isBeingEdited
      }));
  }

  saveItem() {
    let id = this.props.id
    let score = this.state.score
    let url = '/api/grades/' + id + '/'
    console.log(url)
    console.log(parseFloat(this.state.score));
    axios.patch(url, {
        'score': score
    })
    .then(response => {
      this.props.updateParent()
      this.setState(prevState => ({
        isBeingEdited: !prevState.isBeingEdited
      }));
    })
    .catch(error => {
    //   console.log(error.response)
    })
  }

}

export default withStyles(styles)(StudentInfoListItem);
